        background-image: url('../../images/pixel/services/magellan-print.png'); } }

.cat_design, .cat_plugins {
    display: none; }

#skillsDev, #skillsDesign {
    @media (max-width: 582px) {
        transform: scale(.9); }

    @media (max-width: 478px) {
        transform: scale(.72); }

    @media (max-width: 360px) {
        transform: scale(.62); } }

